"use client"

import { useEffect, useState } from "react"
import DashboardHeader from "@/components/dashboard/dashboard-header"
import { Card, CardHeader, CardTitle, CardContent } from "@/components/ui/card"
import { fetchMonitoring } from "@/lib/api"
import {
  ResponsiveContainer,
  LineChart,
  Line,
  CartesianGrid,
  XAxis,
  YAxis,
  Tooltip,
  Legend
} from "recharts"
import { Loader2 } from "lucide-react"

interface MonitoringData {
  accuracy_trend: { date: string; value: number }[]
  current_accuracy: number
  drift: number
  last_retrain: string
  health: "green" | "yellow" | "red"
}

export default function AlertsPage() {
  const [data, setData] = useState<MonitoringData | null>(null)
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    async function load() {
      try {
        setLoading(true)
        const res = await fetchMonitoring({ metric: "cpu", windowDays: 30 })
        setData(res)
      } catch (err) {
        console.error("Failed to load monitoring data:", err)
      } finally {
        setLoading(false)
      }
    }
    load()
  }, [])

  const getHealthLabel = (status: string) => {
    if (status === "green") return "Healthy"
    if (status === "yellow") return "Warning"
    return "Critical"
  }

  const getAlerts = () => {
    if (!data) return []

    const alerts = []

    if (data.health === "red") {
      alerts.push({
        type: "critical",
        message: "‼️ Model accuracy critically low. Immediate retraining recommended."
      })
    }

    if (data.drift > 5) {
      alerts.push({
        type: "warning",
        message: `⚠️ Model drift detected (${data.drift.toFixed(2)}%). Performance degrading.`
      })
    }

    if (data.current_accuracy < 80) {
      alerts.push({
        type: "warning",
        message: `⚠️ Accuracy dropped below 80% (${data.current_accuracy.toFixed(1)}%).`
      })
    }

    if (alerts.length === 0) {
      alerts.push({
        type: "info",
        message: "✅ No alerts — model is performing well."
      })
    }

    return alerts
  }

  return (
    <div className="space-y-6">
      <DashboardHeader
        title="Alerts & Model Monitoring"
        subtitle="Automated detection of drift, accuracy drops, and model degradation"
      />

      {/* Loading */}
      {loading && (
        <div className="flex justify-center py-20">
          <Loader2 className="h-10 w-10 animate-spin text-blue-500" />
        </div>
      )}

      {!loading && data && (
        <>
          {/* Model Health Summary */}
          <Card className="bg-slate-900/50 border-slate-800">
            <CardHeader>
              <CardTitle className="text-white">Model Health Overview</CardTitle>
            </CardHeader>
            <CardContent className="text-slate-300 space-y-3">
              <div className="flex items-center gap-4">
                <div
                  className={`w-4 h-4 rounded-full ${
                    data.health === "green"
                      ? "bg-green-500"
                      : data.health === "yellow"
                      ? "bg-yellow-500"
                      : "bg-red-500"
                  }`}
                />
                <p className="text-lg font-semibold">
                  {getHealthLabel(data.health)}
                </p>
              </div>

              <p><strong>Current Accuracy:</strong> {data.current_accuracy.toFixed(1)}%</p>
              <p><strong>Drift Detected:</strong> {data.drift.toFixed(2)}%</p>
              <p><strong>Last Retrain:</strong> {data.last_retrain}</p>
            </CardContent>
          </Card>

          {/* Accuracy Trend */}
          <Card className="bg-slate-900/50 border-slate-800">
            <CardHeader>
              <CardTitle className="text-white">Accuracy Trend (30 Days)</CardTitle>
            </CardHeader>
            <CardContent>
              <ResponsiveContainer width="100%" height={350}>
                <LineChart data={data.accuracy_trend}>
                  <CartesianGrid strokeDasharray="3 3" stroke="#334155" />
                  <XAxis
                    dataKey="date"
                    stroke="#64748b"
                    tickFormatter={(d) =>
                      new Date(d).toLocaleDateString("en-US", {
                        month: "short",
                        day: "numeric",
                      })
                    }
                  />
                  <YAxis stroke="#64748b" />
                  <Tooltip
                    contentStyle={{
                      backgroundColor: "#1e293b",
                      border: "1px solid #475569",
                      borderRadius: "8px",
                    }}
                  />
                  <Legend />
                  <Line type="monotone" dataKey="value" stroke="#3b82f6" strokeWidth={2} dot={false} name="Accuracy (%)" />
                </LineChart>
              </ResponsiveContainer>
            </CardContent>
          </Card>

          {/* Alerts Section */}
          <Card className="bg-slate-900/50 border-slate-800">
            <CardHeader>
              <CardTitle className="text-white">Active Alerts</CardTitle>
            </CardHeader>
            <CardContent className="space-y-3">
              {getAlerts().map((alert, idx) => (
                <div
                  key={idx}
                  className={`p-3 rounded-lg ${
                    alert.type === "critical"
                      ? "bg-red-900/40 border border-red-700"
                      : alert.type === "warning"
                      ? "bg-yellow-900/30 border border-yellow-700"
                      : "bg-green-900/30 border border-green-700"
                  }`}
                >
                  <p className="text-white">{alert.message}</p>
                </div>
              ))}
            </CardContent>
          </Card>
        </>
      )}
    </div>
  )
}
